.admin-orders__content {
  padding: 0px 50px;
}

.item-table {
  font-family: "PT Sans Caption", sans-serif;
  width: 100%;
}
.item-table__title {
  font-size: 16px;
  font-weight: 700;
  text-transform: uppercase;
  margin-bottom: 15px;
}
.item-table__row {
  font-size: 14px;
  padding: 8px 15px;
}
.item-table__row:not(.item-table__row--header):nth-child(odd) {
  background-color: rgba(3, 124, 212, 0.1);
}
.item-table__row:not(.item-table__row--header):nth-child(even) {
  background-color: #fff;
}
.item-table__row--header {
  background-color: #0956c9;
  color: #ffffff;
}
.item-table__column {
  text-align: center;
  font-size: 16px;
  padding: 15px;
}
.item-table__column--header {
  background-color: #005da0;
}/*# sourceMappingURL=adminOrder.css.map */