.catalog {
  font-family: "PT Sans Caption", sans-serif;
}
.catalog__title {
  font-size: 32px;
  margin-bottom: 25px;
}
.catalog__content {
  display: grid;
  grid-template-columns: 20% 80%;
}
.catalog__filters {
  padding-right: 10px;
}
.catalog__filters .filter-list:not(:last-child) {
  margin-bottom: 20px;
}
.catalog__product-list {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}
.catalog__product-list .product-card {
  max-width: calc(25% - 7.5px);
}
.catalog__no-matches {
  margin: 0 auto;
  font-size: 16px;
}

@media (max-width: 1024px) {
  .catalog__content {
    position: relative;
    display: block;
  }
  .catalog__filters {
    display: none;
    z-index: 100;
    position: fixed;
    overflow: scroll;
    width: 30%;
    background-color: #FFFFFF;
    box-shadow: 2px 1px 8px rgba(0, 0, 0, 0.3);
    padding: 10px;
    top: 0;
    left: 0;
  }
}/*# sourceMappingURL=catalog.css.map */