.product-counter {
  display: flex;
  align-items: center;
}
.product-counter__action {
  cursor: pointer;
  width: 22px;
  height: 22px;
  border-radius: 50%;
  border: none;
  background-color: #037CD4;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 40% 40%;
}
.product-counter__action--add {
  background-image: url("../../../../theme/img/icons/plus.png");
}
.product-counter__action--subtract {
  background-image: url("../../../../theme/img/icons/minus.png");
}
.product-counter__quantity {
  cursor: text;
  display: inline-block;
  width: 40px;
  text-align: center;
  font-size: 18px;
  padding: 0 2px;
}/*# sourceMappingURL=productCounter.css.map */