.link-list {
    display: inline-block;
    font-family: "PT Sans Caption", sans-serif;

    &__title {
        font-family: "PT Sans Caption", sans-serif;
        color: #FFFFFF;
        font-size: 14px;
        font-weight: 700;
        text-transform: uppercase;
        border-bottom: 1px solid #FFFFFF;
        padding-bottom: 10px;
        margin-bottom: 5px;
    }

    &__list {
        padding-right: 20px;
    }

    &__item {

        &:not(:last-child) {
            margin-bottom: 5px;
        }
    }

    &__link {
        cursor: pointer;
        display: inline-block;
        color: #A7A7A7;
        font-size: 14px;
        text-decoration: none;
        line-height: 1.2;

        transition: all .2s ease;

        &:hover {
            color: #037CD4;
        }
    }

    &__text {
        cursor: text;
        display: inline-block;
        color: #A7A7A7;
        font-size: 14px;
        line-height: 1.2;
    }
}