.add-modal {
    font-family: "PT Sans Caption", sans-serif;
    width: 100%;

    &__stats {
        cursor: text;
        text-align: center;
        margin-bottom: 10px;
    }

    &__product-list {
        background-color: #FFFFFF;
        max-height: 300px;
        min-height: 300px;
        overflow-y: scroll;
        -ms-overflow-style: none;
        scrollbar-width: none;
        margin-bottom: 20px;

        &::-webkit-scrollbar {
            width: 0;
        }

        & .cart-product:not(:last-child) {
            border-bottom: 1px solid #E4E4E4;
        }

    }

    &__actions {
        display: flex;
        justify-content: center;
        gap: 20px;
    }

    &__product-count,
    &__total-price {
        font-weight: 700;
        text-wrap: nowrap;
    }
    
}