.search-box__form {
  position: relative;
}
.search-box__field {
  font-family: "PT Sans Caption", sans-serif;
  font-size: 13px;
  width: 503px;
  border-radius: 8px;
  padding: 8px 48px 8px 13px;
  border: 1px solid #E4E4E4;
  background-color: #F1F1F1;
  outline: none;
  color: #000000;
}
.search-box__field::-webkit-search-cancel-button, .search-box__field::-ms-clear {
  display: none;
}
.search-box__submit {
  cursor: pointer;
  position: absolute;
  border: none;
  width: 22px;
  height: 22px;
  background-color: transparent;
  background-image: url("/src/theme/img/icons/loop.png");
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  top: 50%;
  right: 13px;
  transform: translateY(-50%);
}/*# sourceMappingURL=searchBox.css.map */