.order {
  font-family: "PT Sans Caption", sans-serif;
}
.order__order-form {
  max-width: 100%;
}
.order__content {
  display: flex;
  gap: 20px;
}
.order__cart {
  max-width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #E4E4E4;
  border-radius: 8px;
  box-shadow: 1px 1px 6px rgba(0, 0, 0, 0.2);
  padding: 20px;
}
.order__block {
  width: 50%;
}
.order__stats {
  cursor: text;
  text-align: center;
  margin-bottom: 10px;
}
.order__product-list {
  background-color: #FFFFFF;
  max-height: 300px;
  min-height: 300px;
  overflow-y: scroll;
  -ms-overflow-style: none;
  scrollbar-width: none;
  margin-bottom: 20px;
}
.order__product-list::-webkit-scrollbar {
  width: 0;
}
.order__product-list .cart-product:not(:last-child) {
  border-bottom: 1px solid #E4E4E4;
}
.order__actions {
  display: flex;
  justify-content: center;
  gap: 20px;
}
.order__product-count, .order__total-price {
  font-weight: 700;
  text-wrap: nowrap;
}
.order__empty-cart {
  font-size: 16px;
}
.order__empty-warning {
  margin-bottom: 10px;
}
.order__success {
  text-align: center;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #E4E4E4;
  border-radius: 8px;
  box-shadow: 1px 1px 6px rgba(0, 0, 0, 0.2);
  padding: 40px 20px;
}
.order__success-img {
  max-width: 250px;
  margin: 0 auto;
  margin-bottom: 20px;
}
.order__success-img img {
  width: 100%;
  display: block;
}
.order__order-status {
  font-size: 22px;
  font-weight: 700;
  margin-bottom: 15px;
}
.order__text-link {
  cursor: pointer;
  display: inline-block;
  color: #037CD4;
  font-size: 16px;
}

.order-form {
  font-family: "PT Sans Caption", sans-serif;
  background-color: #FFFFFF;
  border: 1px solid #E4E4E4;
  border-radius: 8px;
  box-shadow: 1px 1px 6px rgba(0, 0, 0, 0.2);
  padding: 20px;
}
.order-form__body {
  margin-bottom: 20px;
}
.order-form__field-label {
  display: inline-block;
  margin-bottom: 10px;
}
.order-form__field {
  max-width: 100%;
}
.order-form__input {
  width: 100%;
}
.order-form__section {
  display: flex;
  max-width: 100%;
  gap: 20px;
  margin-bottom: 20px;
}
.order-form__error {
  font-size: 12px;
  color: #f85252;
  background-color: #ffe3e3;
  border: 1px solid #ff9797;
  border-radius: 8px;
  padding: 5px;
  margin-bottom: 10px;
}

@media (max-width: 1280px) {
  .order .cart-product__price {
    font-size: 14px;
    width: 80px;
  }
  .order .cart-product__img {
    width: 40px;
  }
  .order .cart-product__title {
    font-size: 12px;
  }
  .order .cart-product__articul {
    font-size: 10px;
  }
  .order .cart-product__desc {
    width: 140px;
    margin-right: 15px;
  }
  .order .product-counter__quantity {
    font-size: 14px;
    width: 30px;
  }
  .order .product-counter__action {
    width: 20px;
    height: 20px;
  }
}/*# sourceMappingURL=order.css.map */