.dropdown {
    font-family: "PT Sans Caption", sans-serif;
    font-size: 13px;
    line-height: 1.2;
    position: relative;
    width: 100%;
    user-select: none;

    &__selected {
        padding: 8px 13px;
        border: 1px solid #E4E4E4;
        border-radius: 8px;
        cursor: pointer;
        background-color: #F1F1F1;

        &--active {
            border-radius: 8px 8px 0px 0px;
        }

        &::after {
            content: '';
            position: absolute;
            width: 12px;
            height: 12px;
            background-image: url("../../../../theme/img/icons/chevron-blue.png");
            background-repeat: no-repeat;
            background-position: center center;
            background-size: cover;
            right: 10px;
            top: 50%;
            transform: translateY(-50%) rotateZ(90deg);
        }
    }

    &__list {
        position: absolute;
        top: 100%;
        left: 0;
        width: 100%;
        border: 1px solid #E4E4E4;
        border-radius: 0px 0px 8px 8px;
        background-color: #F1F1F1;
        z-index: 10;
    }

    &__option {
        padding: 10px;
        cursor: pointer;
        border-bottom: 1px solid #E4E4E4;
        transition: all .3s ease;

        &:hover {
            background-color: #dbdbdb;
        }
    }

}