.product-page {
  font-family: "PT Sans Caption", sans-serif;
}
.product-page__header {
  display: flex;
  gap: 20px;
  margin-bottom: 30px;
}
.product-page__img {
  padding: 20px;
  background-color: #FFFFFF;
  border: 1px solid #E4E4E4;
  border-radius: 8px;
  box-shadow: 1px 1px 6px rgba(0, 0, 0, 0.2);
  width: 50%;
}
.product-page__img img {
  display: block;
  width: 100%;
}
.product-page__info {
  width: 50%;
}
.product-page__actions {
  display: flex;
  align-items: center;
  background-color: #FFFFFF;
  max-width: 340px;
  justify-content: space-between;
  border: 1px solid #E4E4E4;
  border-radius: 8px;
  box-shadow: 1px 1px 6px rgba(0, 0, 0, 0.2);
  padding: 25px 15px;
}
.product-page__name {
  font-size: 32px;
  margin-bottom: 10px;
}
.product-page__articul {
  font-size: 14px;
  color: #A7A7A7;
}
.product-page__title {
  margin-bottom: 20px;
}
.product-page__price {
  font-size: 32px;
  font-weight: 700;
  margin-right: 40px;
  white-space: nowrap;
}
.product-page__error {
  text-align: center;
}
.product-page__details {
  width: 100%;
  display: flex;
  gap: 20px;
}
.product-page__specs-table {
  background-color: #FFFFFF;
  border: 1px solid #E4E4E4;
  border-radius: 8px;
  box-shadow: 1px 1px 6px rgba(0, 0, 0, 0.2);
  padding: 20px;
}
.product-page__desc {
  background-color: #FFFFFF;
  border: 1px solid #E4E4E4;
  border-radius: 8px;
  box-shadow: 1px 1px 6px rgba(0, 0, 0, 0.2);
  padding: 20px;
}
.product-page__desc-title {
  font-size: 16px;
  font-weight: 700;
  text-transform: uppercase;
  margin-bottom: 15px;
}
.product-page__desc-text {
  line-height: 1.2;
  font-size: 14px;
}
.product-page__desc-text p:not(:last-child) {
  margin-bottom: 15px;
}
.product-page__info-block {
  width: 100%;
}

.specs-table {
  font-family: "PT Sans Caption", sans-serif;
}
.specs-table__title {
  font-size: 16px;
  font-weight: 700;
  text-transform: uppercase;
  margin-bottom: 15px;
}
.specs-table__body {
  display: flex;
  flex-direction: column;
}
.specs-table__row {
  display: flex;
  justify-content: space-between;
  font-size: 14px;
  padding: 8px 15px;
}
.specs-table__row:nth-child(odd) {
  background-color: rgba(3, 124, 212, 0.1);
}
.specs-table__row:nth-child(even) {
  background-color: #fff;
}
.specs-table__column:nth-child(odd) {
  text-align: left;
}
.specs-table__column:nth-child(even) {
  text-align: right;
}

.no-matches {
  text-align: center;
}
.no-matches__img {
  display: inline-block;
  opacity: 0.1;
  max-width: 300px;
  margin-bottom: 20px;
}
.no-matches__img img {
  display: block;
  width: 100%;
}

@media (max-width: 1023px) {
  .product-page__details {
    display: block;
    gap: 0;
  }
  .product-page__info-block:not(:last-child) {
    margin-bottom: 20px;
  }
}/*# sourceMappingURL=productDetails.css.map */