.profile {
    font-family: "PT Sans Caption", sans-serif;
    
    &__icon {
        cursor: pointer;
        position: relative;
        width: 28px;
        height: 28px;

        & img {
            width: 100%;
            display: block;
        }
    }

    &__authorized {
        position: relative;

        &:hover .profile__actions {
            visibility: visible;
            opacity: 1;
        }
    }

    &__login {
        cursor: pointer;
        position: relative;
        font-size: 16px;

        &::before {
            content: "";
            position: absolute;
            width: 16px;
            height: 16px;
            background-image: url("../../../../theme/img/icons/auth-user.png");
            background-repeat: no-repeat;
            background-position: center center;
            background-size: cover;

            left: -21px;
            top: 50%;
            transform: translateY(-50%);
        }

        &::after {
            content: "";
            position: absolute;
            width: 100%;
            height: 1px;
            background-color: #000000;

            bottom: -2px;
            left: 50%;
            transform: translateX(-50%);
        }
    }

    &__logout {
        cursor: pointer;
        display: inline-block;
        position: relative;
        font-size: 14px;
        background-color: transparent;
        border: none;
        outline: none;
        color: #000000;
        padding: 0;
        transition: all .3s ease;

        &::after {
            content: "";
            position: absolute;
            width: 100%;
            height: 1px;
            background-color: #000000;

            bottom: -1px;
            left: 50%;
            transform: translateX(-50%);
        }
    }

    &__actions {
        opacity: 0;
        visibility: hidden;
        position: absolute;
        background-color: #FFFFFF;
        box-shadow: 1px 1px 6px rgba($color: #000000, $alpha: 0.2);
        border-radius: 8px;
        padding: 10px 20px;

        top: 30px;
        left: 50%;
        transform: translateX(-50%);
        transition: all .3s ease;

        &:hover {
            visibility: visible;
            opacity: 1;
        }

        &::before {
            content: "";
            position: absolute;
            z-index: 50;
            width: 16px;
            height: 16px;
            background-color: #FFFFFF;
            box-shadow: -2px -2px 3px -1px rgba(0, 0, 0, 0.2);

            top: -8px;
            left: 50%;
            transform: translateX(-50%) rotateZ(45deg);
        }
    }
}