.submit {
  cursor: pointer;
  display: block;
  font-family: "PT Sans Caption", sans-serif;
  font-size: 16px;
  color: white;
  background-color: #037CD4;
  border-radius: 8px;
  padding: 7px 33px;
  border: none;
  outline: none;
  transition: all 0.4s ease;
  line-height: 1.2;
}
.submit:hover {
  background-color: #036bb6;
