.cart-product {
    cursor: default;
    display: flex;
    align-items: center;
    font-family: "PT Sans Caption", sans-serif;
    padding: 10px 10px;

    &__info {
        display: flex;
        align-items: center;
    }

    &__cancel {
        flex: 0 0 auto;
        cursor: pointer;
        width: 18px;
        height: 18px;
        background-color: transparent;
        background-image: url("../../../../theme/img/icons/cancel.png");
        background-repeat: no-repeat;
        background-position: center center;
        background-size: cover;
        border: none;
        margin-right: 10px;
    }

    &__item {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
    }

    &__img {
        cursor: pointer;
        display: block;
        width: 60px;
        padding: 5px;
        border-radius: 8px;
        border: 1px solid #E4E4E4;
        margin-right: 10px;

        & img {
            display: block;
            width: 100%;
        }
    }

    &__desc {
        display: block;
        width: 200px;
        margin-right: 30px;
    }

    &__title {
        cursor: pointer;
        display: block;
        text-decoration: none;
        color: #000000;
        margin-bottom: 5px;

        transition: all .2s ease;

        &:hover {
            color: #037CD4;
        }
    }

    &__articul {
        cursor: text;
        display: block;
        color: #A7A7A7;
        font-size: 12px;
    }

    &__product-counter {
        width: 100px;
        margin-right: 15px;
        justify-content: center;
    }

    &__price {
        cursor: text;
        width: 120px;
        font-weight: 700;
        text-align: end;
    }

    &__currency {
        display: inline-block;
        font-weight: 700;
    }

}