.order {
    font-family: "PT Sans Caption", sans-serif;

    &__order-form {
        max-width: 100%;
    }

    &__content {
        display: flex;
        gap: 20px;
    }

    &__cart {
        max-width: 100%;
        background-color: #FFFFFF;
        border: 1px solid #E4E4E4;
        border-radius: 8px;
        box-shadow: 1px 1px 6px rgba(0, 0, 0, 0.2);
        padding: 20px;
    }

    &__block {
        width: 50%;
    }

    &__stats {
        cursor: text;
        text-align: center;
        margin-bottom: 10px;
    }

    &__product-list {
        background-color: #FFFFFF;
        max-height: 300px;
        min-height: 300px;
        overflow-y: scroll;
        -ms-overflow-style: none;
        scrollbar-width: none;
        margin-bottom: 20px;

        &::-webkit-scrollbar {
            width: 0;
        }

        & .cart-product:not(:last-child) {
            border-bottom: 1px solid #E4E4E4;
        }

    }

    &__actions {
        display: flex;
        justify-content: center;
        gap: 20px;
    }

    &__product-count,
    &__total-price {
        font-weight: 700;
        text-wrap: nowrap;
    }

    &__empty-cart {
        font-size: 16px;
    }

    &__empty-warning {
        margin-bottom: 10px;
    }

    &__success {
        text-align: center;
        width: 100%;
        background-color: #FFFFFF;
        border: 1px solid #E4E4E4;
        border-radius: 8px;
        box-shadow: 1px 1px 6px rgba(0, 0, 0, 0.2);
        padding: 40px 20px;
    }

    &__success-img {
        max-width: 250px;
        margin: 0 auto;
        margin-bottom: 20px;

        & img {
            width: 100%;
            display: block;
        }
    }

    &__order-status {
        font-size: 22px;
        font-weight: 700;
        margin-bottom: 15px;
    }

    &__text-link {
        cursor: pointer;
        display: inline-block;
        color: #037CD4;
        font-size: 16px;
    }

    
}

.order-form {
    font-family: "PT Sans Caption", sans-serif;
    background-color: #FFFFFF;
    border: 1px solid #E4E4E4;
    border-radius: 8px;
    box-shadow: 1px 1px 6px rgba(0, 0, 0, 0.2);
    padding: 20px;


    &__body {
        margin-bottom: 20px;
    }

    &__field-label {
        display: inline-block;
        margin-bottom: 10px;
    }

    &__field {
        max-width: 100%;
    }
    
    &__input {
        width: 100%;
    }

    &__section {
        display: flex;
        max-width: 100%;
        gap: 20px;
        margin-bottom: 20px;
    }

    &__error {
        font-size: 12px;
        color: #f85252;
        background-color: #ffe3e3;
        border: 1px solid #ff9797;
        border-radius: 8px;
        padding: 5px;
        margin-bottom: 10px;
    }

}

@media (max-width: 1280px) {
    .order {
        & .cart-product {
            &__price {
                font-size: 14px;
                width: 80px;
            }
    
            &__img {
                width: 40px;
            }
            
            &__title {
                font-size: 12px;
            }
    
            &__articul {
                font-size: 10px;
            }
    
            &__desc {
                width: 140px;
                margin-right: 15px;
            }
        }
    
        & .product-counter {
            &__quantity {
                font-size: 14px;
                width: 30px;
            }
    
            &__action {
                width: 20px;
                height: 20px;
            }
        }
    }
}