.admin-page {
  font-family: "PT Sans Caption", sans-serif;
  background-color: #f8f8f8;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.admin-header {
  position: fixed;
  width: 100%;
  background-color: #0956c9;
  padding: 25px 50px;
}
.admin-header__title {
  text-transform: uppercase;
  color: #ffffff;
  font-size: 32px;
  font-weight: 700;
}
.admin-header__nav {
  display: flex;
  gap: 30px;
}
.admin-header__nav-link {
  cursor: pointer;
  text-decoration: none;
  color: #ffffff;
  font-size: 22px;
  transition: all 0.3s ease;
}
.admin-header__nav-link:hover {
  color: #d3d3d3;
}
.admin-header__content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.main {
  padding-top: 140px;
  margin-bottom: 40px;
}/*# sourceMappingURL=adminPage.css.map */