.header {
    z-index: 40;
    position: fixed;
    background-color: #FFFFFF;
    width: 100%;
    box-shadow: 1px 1px 6px rgba(0, 0, 0, 0.2);
    font-family: "PT Sans Caption", sans-serif;
    
    &__logo {
        max-width: 160px;
    }

    &__content {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-top: 21px;
        padding-bottom: 21px;
    }

    &__nav {
        display: flex;
        flex-wrap: nowrap;
        background-color: #363636;
    }
}

.nav {

    &__list {
        display: flex;
        justify-content: center;
    }

    &__list-item {
        cursor: pointer;
        color: #FFFFFF;
        padding: 11px 20px;
        border-right: 2px solid #606060;

        transition: all .2s ease;

        &:hover {
            background-color: #555555;
        }

        &:first-child {
            border-left: 2px solid #606060;
        }

        &--ctg {
            position: relative;
            padding: 11px 20px 11px 45px;

            &:hover {
                background-color: #036bb6;
            }

            &::before {
                content: "";
                position: absolute;
                width: 16px;
                height: 16px;
                background-image: url("../../../../theme/img/icons/categories.png");
                background-repeat: no-repeat;
                background-position: center center;
                background-size: 100% 100%;

                left: 20px;
                top: 50%;
                transform: translateY(-50%);
            }
        }
    }

    &__link {
        font-size: 14px;
        color: #FFFFFF;
        text-decoration: none;
    }
}

.actions-list {
    display: flex;
    align-items: center;

    &__item {

        &:not(:last-child) {
            margin-right: 24px;   
        }
    }
}