.admin-page {
    font-family: "PT Sans Caption", sans-serif;
    background-color: #f8f8f8;
    //background-color: red;
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

.admin-header {
    position: fixed;
    width: 100%;
    background-color: #0956c9;
    padding: 25px 50px;

    &__title {
        text-transform: uppercase;
        color: #ffffff;
        font-size: 32px;
        font-weight: 700;
    }

    &__nav {
        display: flex;
        gap: 30px;
    }

    &__nav-item {

    }

    &__nav-link {
        cursor: pointer;
        text-decoration: none;
        color: #ffffff;
        font-size: 22px;
        transition: all .3s ease;

        &:hover {
            color: #d3d3d3;
        }
    }

    &__content {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}

.main {
    padding-top: 140px;
    margin-bottom: 40px;
}
