.product-card {
  cursor: default;
  display: flex;
  flex-direction: column;
  font-family: "PT Sans Caption", sans-serif;
  border: 1px solid #E4E4E4;
  border-radius: 8px;
  padding: 15px;
  background-color: #FFFFFF;
  transition: all 0.3s;
}
.product-card:hover {
  box-shadow: 1px 1px 6px rgba(0, 0, 0, 0.2);
}
.product-card__image {
  display: block;
  cursor: pointer;
  max-width: 100%;
  margin-bottom: 10px;
}
.product-card__image img {
  display: block;
  width: 100%;
}
.product-card__title {
  cursor: pointer;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  flex-shrink: 1;
  color: #000000;
  text-decoration: none;
  font-size: 14px;
  height: 3.6em;
  font-weight: 400;
  line-height: 1.2;
  margin-bottom: 10px;
  transition: all 0.2s ease;
}
.product-card__title:hover {
  color: #037CD4;
}
.product-card__bottom {
  margin-top: auto;
}
.product-card__price {
  cursor: text;
  display: inline-block;
  margin-bottom: 16px;
}
.product-card__sum {
  font-size: 24px;
  font-weight: 700;
}
.product-card__currency {
  font-size: 18px;
  font-weight: 700;
}/*# sourceMappingURL=product.css.map */