.footer {
  background-color: #363636;
  font-family: "PT Sans Caption", sans-serif;
  margin-top: auto;
  padding: 30px 0 10px 0;
}
.footer__content {
  display: flex;
  justify-content: space-between;
  margin-bottom: 40px;
}
.footer__links {
  display: flex;
  align-items: start;
}
.footer__link-list:not(:last-child) {
  margin-right: 60px;
}
.footer__wrapper {
  vertical-align: middle;
}
.footer__payment-list {
  display: flex;
  gap: 10px;
  align-items: center;
}
.footer__payment-method {
  opacity: 0.35;
  display: inline-block;
  max-width: 100px;
}
.footer__payment-method img {
  display: block;
  width: 100%;
}
.footer__copyright {
  text-align: center;
  font-size: 12px;
  line-height: 1.2;
  color: #A7A7A7;
  border-top: 1px solid #FFFFFF;
  padding: 10px 0 0 0;
  margin: 0 40px;
}/*# sourceMappingURL=footer.css.map */