.banner {
  position: relative;
  width: 100%;
  overflow: hidden;
  margin-bottom: 30px;
}
.banner__slides {
  display: flex;
  transition: transform 0.8s ease-in-out;
  margin-bottom: 15px;
}
.banner__slide {
  min-width: 100%;
}
.banner__image {
  cursor: pointer;
  width: 100%;
  display: block;
}
.banner__dots {
  width: 100%;
  display: flex;
  justify-content: center;
  gap: 10px;
}
.banner__dot {
  display: inline-block;
  width: 12px;
  height: 12px;
  background-color: #D9D9D9;
  border-radius: 50%;
  cursor: pointer;
}
.banner__dot--active {
  background-color: #037CD4;
}/*# sourceMappingURL=banner.css.map */