.featured-products {
    padding: 40px 0;
    background-color: #fff;
    box-shadow: 1px 1px 6px rgba(0, 0, 0, 0.2);
    font-family: "PT Sans Caption", sans-serif;

    &__header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 20px;
    }

    &__link {
        position: relative;
        display: inline-block;
        color: #037CD4;
        text-decoration: none;

        &::after {
            content: "";
            position: absolute;
            width: 100%;
            height: 1px;
            background-color: #037CD4;

            bottom: -1px;
            left: 50%;
            transform: translateX(-50%);
        }
    }

    &__title {
        font-size: 24px;
        font-weight: 700;
    }

    &__list {
        display: flex;
        gap: 10px;
    }

    &__list-item {
        
    }
}