.modal-window {
    font-family: "PT Sans Caption", sans-serif;
    position: fixed;
    z-index: 150;
    background-color: rgb(0, 0, 0, 0.2);
    width: 100%;
    height: 100%;

    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);

    &__body {
        position: absolute;
        background-color: #FFFFFF;
        padding: 25px;
        border: 1px solid #E4E4E4;
        border-radius: 8px;
        box-shadow: 1px 1px 6px rgba(0, 0, 0, 0.2);

        top: 50%;
        left: 50%;
        transform: translateX(-50%) translateY(-50%);
    }

    &__close {
        cursor: pointer;
        position: absolute;

        top: 7px;
        right: 18px;
        

        &::before, 
        &::after {
            content: "";
            position: absolute;
            width: 3px;
            height: 18px;
            border-radius: 3px;
            background-color: #000000;
            left: 50%;
            transform: translateX(-50%);
        }

        &::before {
            transform: rotateZ(45deg);
        }

        &::after {
            transform: rotateZ(-45deg);
        }
    }
}