.catalog {
    font-family: "PT Sans Caption", sans-serif;

    &__title {
        font-size: 32px;
        margin-bottom: 25px;
    }

    &__content {
        display: grid;
        grid-template-columns: 20% 80%;
    }

    &__filters {
        padding-right: 10px;

        & .filter-list:not(:last-child) {
            margin-bottom: 20px;
        }
    }

    &__products {

    }

    &__product-list {

        display: flex;
        flex-wrap: wrap;
        gap: 10px;

        & .product-card {
            max-width: calc(25% - 7.5px);
        }
    }

    &__no-matches {
        margin: 0 auto;
        font-size: 16px;
    }
}

@media (max-width: 1024px) {
    .catalog {

        &__content {
            position: relative;
            display: block;
        }

        &__filters {
            display: none;
            z-index: 100;
            position: fixed;
            overflow: scroll;
            width: 30%;
            background-color: #FFFFFF;
            box-shadow: 2px 1px 8px rgba(0, 0, 0, 0.3);
            padding: 10px;
            top: 0;
            left: 0;
        }
    }
}