.link-list {
  display: inline-block;
  font-family: "PT Sans Caption", sans-serif;
}
.link-list__title {
  font-family: "PT Sans Caption", sans-serif;
  color: #FFFFFF;
  font-size: 14px;
  font-weight: 700;
  text-transform: uppercase;
  border-bottom: 1px solid #FFFFFF;
  padding-bottom: 10px;
  margin-bottom: 5px;
}
.link-list__list {
  padding-right: 20px;
}
.link-list__item:not(:last-child) {
  margin-bottom: 5px;
}
.link-list__link {
  cursor: pointer;
  display: inline-block;
  color: #A7A7A7;
  font-size: 14px;
  text-decoration: none;
  line-height: 1.2;
  transition: all 0.2s ease;
}
.link-list__link:hover {
  color: #037CD4;
}
.link-list__text {
  cursor: text;
  display: inline-block;
  color: #A7A7A7;
  font-size: 14px;
  line-height: 1.2;
}/*# sourceMappingURL=linkList.css.map */