.product-page {
    font-family: "PT Sans Caption", sans-serif;
    
    &__header {
        display: flex;
        gap: 20px;
        margin-bottom: 30px;
    }

    &__img {
        padding: 20px;
        background-color: #FFFFFF;
        border: 1px solid #E4E4E4;
        border-radius: 8px;
        box-shadow: 1px 1px 6px rgba(0, 0, 0, 0.2);
        width: 50%;

        & img {
            display: block;
            width: 100%;
        }
    }

    &__info {
        width: 50%;
    }

    &__actions {
        display: flex;
        align-items: center;
        background-color: #FFFFFF;
        max-width: 340px;
        justify-content: space-between;
        border: 1px solid #E4E4E4;
        border-radius: 8px;
        box-shadow: 1px 1px 6px rgba(0, 0, 0, 0.2);
        padding: 25px 15px;
    }

    &__name {
        font-size: 32px;
        margin-bottom: 10px;
    }

    &__articul {
        font-size: 14px;
        color: #A7A7A7;
    }

    &__title {
        margin-bottom: 20px;
    }

    &__price {
        font-size: 32px;
        font-weight: 700;
        margin-right: 40px;
        white-space: nowrap;
    }

    &__error {
        text-align: center;
    }

    &__details {
        width: 100%;
        display: flex;
        gap: 20px;
    }

    &__specs-table {
        background-color: #FFFFFF;
        border: 1px solid #E4E4E4;
        border-radius: 8px;
        box-shadow: 1px 1px 6px rgba(0, 0, 0, 0.2);
        padding: 20px;
    }

    &__desc {
        background-color: #FFFFFF;
        border: 1px solid #E4E4E4;
        border-radius: 8px;
        box-shadow: 1px 1px 6px rgba(0, 0, 0, 0.2);
        padding: 20px;
    }

    &__desc-title {
        font-size: 16px;
        font-weight: 700;
        text-transform: uppercase;
        margin-bottom: 15px;
    }

    &__desc-text {
        line-height: 1.2;
        font-size: 14px;

        & p:not(:last-child) {
            margin-bottom: 15px;
        }
    }

    &__info-block {
        width: 100%;
    }
}

.specs-table {
    font-family: "PT Sans Caption", sans-serif;

    &__title {
        font-size: 16px;
        font-weight: 700;
        text-transform: uppercase;
        margin-bottom: 15px;
    }

    &__body {
        display: flex;
        flex-direction: column;
    }

    &__row {
        display: flex;
        justify-content: space-between;
        font-size: 14px;
        padding: 8px 15px;

        &:nth-child(odd) {
            background-color: rgba($color: #037CD4, $alpha: 0.1);
        }

        &:nth-child(even) {
            background-color: #fff;
        }
    }

    &__column {
        &:nth-child(odd) {
            text-align: left;
        }

        &:nth-child(even) {
            text-align: right;
        }
    }
}

.no-matches {
    text-align: center;

    &__img {
        display: inline-block;
        opacity: 0.1;
        max-width: 300px;
        margin-bottom: 20px;

        & img {
            display: block;
            width: 100%;
        }
    }
}

@media (max-width: 1023px) {
    .product-page {
        
        &__details {
            display: block;
            gap: 0;
        }

        &__info-block {
            &:not(:last-child) {
                margin-bottom: 20px;
            }
        }
    }
}