.auth-form {
  width: 300px;
  font-family: "PT Sans Caption", sans-serif;
}
.auth-form__body {
  margin-bottom: 20px;
}
.auth-form__field-label {
  display: inline-block;
  margin-bottom: 10px;
}
.auth-form__field {
  max-width: 100%;
}
.auth-form__input:not(:last-child) {
  margin-bottom: 15px;
}
.auth-form__submit {
  margin-right: 10px;
}
.auth-form__link {
  cursor: pointer;
  display: block;
  background-color: transparent;
  border: none;
  outline: none;
  position: relative;
  color: #037CD4;
  text-decoration: none;
  padding: 0;
}
.auth-form__link::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 1px;
  background-color: #037CD4;
  bottom: -1px;
  left: 50%;
  transform: translateX(-50%);
}
.auth-form__actions {
  display: flex;
  align-items: center;
}
.auth-form__error {
  font-size: 12px;
  color: #f85252;
  background-color: #ffe3e3;
  border: 1px solid #ff9797;
  border-radius: 8px;
  padding: 5px;
  margin-bottom: 10px;
}/*# sourceMappingURL=authModalForm.css.map */