.admin-orders {
    &__content {
        padding: 0px 50px;
    }
}

.item-table {
    font-family: "PT Sans Caption", sans-serif;
    width: 100%;

    &__title {
        font-size: 16px;
        font-weight: 700;
        text-transform: uppercase;
        margin-bottom: 15px;
    }

    &__body {
        // display: flex;
        // flex-direction: column;
    }

    &__row {
        font-size: 14px;
        padding: 8px 15px;
        
        &:not(&--header) {
            &:nth-child(odd) {
                background-color: rgba($color: #037CD4, $alpha: 0.1);
            }
    
            &:nth-child(even) {
                background-color: #fff;
            }
        }

        &--header {
            background-color: #0956c9;
            color: #ffffff
        }
    }

    &__column {
        text-align: center;
        font-size: 16px;
        padding: 15px;

        &--header {
            background-color: #005da0;
        }
    }
}