.page {
  background-color: #f8f8f8;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.main {
  padding-top: 140px;
  margin-bottom: 40px;
}

.info-block {
  font-family: "PT Sans Caption", sans-serif;
  line-height: 1.2;
  margin-bottom: 40px;
}
.info-block__content {
  background-color: #FFFFFF;
  border-radius: 8px;
  border: 1px solid #E4E4E4;
  box-shadow: 1px 1px 6px rgba(0, 0, 0, 0.2);
  padding: 20px 15px;
}
.info-block__section:not(:last-child) {
  margin-bottom: 25px;
}
.info-block__title {
  text-align: center;
  font-size: 24px;
  font-weight: 700;
  text-transform: uppercase;
  margin-bottom: 20px;
}
.info-block__subtitle {
  font-size: 18px;
  font-weight: 700;
  margin-bottom: 15px;
}
.info-block__text {
  font-size: 14px;
}
.info-block__text--bold {
  font-weight: 700;
}
.info-block__text:not(:last-child) {
  margin-bottom: 10px;
}/*# sourceMappingURL=page.css.map */