.profile {
  font-family: "PT Sans Caption", sans-serif;
}
.profile__icon {
  cursor: pointer;
  position: relative;
  width: 28px;
  height: 28px;
}
.profile__icon img {
  width: 100%;
  display: block;
}
.profile__authorized {
  position: relative;
}
.profile__authorized:hover .profile__actions {
  visibility: visible;
  opacity: 1;
}
.profile__login {
  cursor: pointer;
  position: relative;
  font-size: 16px;
}
.profile__login::before {
  content: "";
  position: absolute;
  width: 16px;
  height: 16px;
  background-image: url("../../../../theme/img/icons/auth-user.png");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  left: -21px;
  top: 50%;
  transform: translateY(-50%);
}
.profile__login::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 1px;
  background-color: #000000;
  bottom: -2px;
  left: 50%;
  transform: translateX(-50%);
}
.profile__logout {
  cursor: pointer;
  display: inline-block;
  position: relative;
  font-size: 14px;
  background-color: transparent;
  border: none;
  outline: none;
  color: #000000;
  padding: 0;
  transition: all 0.3s ease;
}
.profile__logout::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 1px;
  background-color: #000000;
  bottom: -1px;
  left: 50%;
  transform: translateX(-50%);
}
.profile__actions {
  opacity: 0;
  visibility: hidden;
  position: absolute;
  background-color: #FFFFFF;
  box-shadow: 1px 1px 6px rgba(0, 0, 0, 0.2);
  border-radius: 8px;
  padding: 10px 20px;
  top: 30px;
  left: 50%;
  transform: translateX(-50%);
  transition: all 0.3s ease;
}
.profile__actions:hover {
  visibility: visible;
  opacity: 1;
}
.profile__actions::before {
  content: "";
  position: absolute;
  z-index: 50;
  width: 16px;
  height: 16px;
  background-color: #FFFFFF;
  box-shadow: -2px -2px 3px -1px rgba(0, 0, 0, 0.2);
  top: -8px;
  left: 50%;
  transform: translateX(-50%) rotateZ(45deg);
}/*# sourceMappingURL=profile.css.map */