.button {
    cursor: pointer;
    display: inline-block;
    font-family: "PT Sans Caption", sans-serif;
    font-size: 16px;
    color: white;
    text-decoration: none;
    background-color: #037CD4;
    border-radius: 8px;
    padding: 7px 33px;
    border: none;
    outline: none;
    transition: all .4s ease;
    line-height: 1.2;

    &:hover {
        background-color: #036bb6;
    }
}