.product-counter {
    display: flex;
    align-items: center;

    &__action {
        cursor: pointer;
        width: 22px;
        height: 22px;
        border-radius: 50%;
        border: none;
        background-color: #037CD4;
        background-repeat: no-repeat;
        background-position: center center;
        background-size: 40% 40%;
    
        &--add {
            background-image: url("../../../../theme/img/icons/plus.png");
        }
    
        &--subtract {
            background-image: url("../../../../theme/img/icons/minus.png");
        }
    }
    
    &__quantity {
        cursor: text;
        display: inline-block;
        width: 40px;
        text-align: center;
        font-size: 18px;
        padding: 0 2px;
    }
}