.banner {
    position: relative;
    width: 100%;
    overflow: hidden;
    margin-bottom: 30px;
  
    &__slides {
      display: flex;
      transition: transform 0.8s ease-in-out;
      margin-bottom: 15px;
    }
  
    &__slide {
      min-width: 100%;
    }
  
    &__image {
      cursor: pointer;
      width: 100%;
      display: block;
    }
  
    &__dots {
      width: 100%;
      display: flex;
      justify-content: center;
      gap: 10px;
    }
  
    &__dot {
      display: inline-block;
      width: 12px;
      height: 12px;
      background-color: #D9D9D9;
      border-radius: 50%;
      cursor: pointer;
  
      &--active {
        background-color: #037CD4;
      }

    }
  }
  