.header {
  z-index: 40;
  position: fixed;
  background-color: #FFFFFF;
  width: 100%;
  box-shadow: 1px 1px 6px rgba(0, 0, 0, 0.2);
  font-family: "PT Sans Caption", sans-serif;
}
.header__logo {
  max-width: 160px;
}
.header__content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 21px;
  padding-bottom: 21px;
}
.header__nav {
  display: flex;
  flex-wrap: nowrap;
  background-color: #363636;
}

.nav__list {
  display: flex;
  justify-content: center;
}
.nav__list-item {
  cursor: pointer;
  color: #FFFFFF;
  padding: 11px 20px;
  border-right: 2px solid #606060;
  transition: all 0.2s ease;
}
.nav__list-item:hover {
  background-color: #555555;
}
.nav__list-item:first-child {
  border-left: 2px solid #606060;
}
.nav__list-item--ctg {
  position: relative;
  padding: 11px 20px 11px 45px;
}
.nav__list-item--ctg:hover {
  background-color: #036bb6;
}
.nav__list-item--ctg::before {
  content: "";
  position: absolute;
  width: 16px;
  height: 16px;
  background-image: url("../../../../theme/img/icons/categories.png");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 100% 100%;
  left: 20px;
  top: 50%;
  transform: translateY(-50%);
}
.nav__link {
  font-size: 14px;
  color: #FFFFFF;
  text-decoration: none;
}

.actions-list {
  display: flex;
  align-items: center;
}
.actions-list__item:not(:last-child) {
  margin-right: 24px;
}/*# sourceMappingURL=header.css.map */