.breadcrumbs {
  font-family: "PT Sans Caption", sans-serif;
  margin-bottom: 25px;

  &__list {
    display: flex;
    list-style: none;
    padding: 0;
    margin: 0;
  }

  &__link {
    text-decoration: none;
    color: #000000;
  }

  &__current {
    color: #A7A7A7;
  }

  &__list-item {
    position: relative;
    font-size: 14px;
    margin-right: 26px;

    &::after {
      content: "";
      position: absolute;
      width: 10px;
      height: 10px;
      background-image: url("../../../../theme/img/icons/chevron-blue.png");
      background-repeat: no-repeat;
      background-position: center center;
      background-size: cover;

      right: -18px;
      top: 50%;
      transform: translateY(-50%);
    }

    &:last-child::after {
      display: none;
    }
  }

}

  
