.not-found {
  text-align: center;
  font-family: "PT Sans Caption", sans-serif;
}
.not-found__image {
  display: inline-block;
  width: 30%;
  opacity: 0.2;
  margin-bottom: 30px;
}
.not-found__image img {
  display: block;
  width: 100%;
}
.not-found__title {
  color: #000000;
  font-size: 26px;
  font-weight: 700;
  margin-bottom: 5px;
}
.not-found__description {
  color: #A7A7A7;
  font-size: 16px;
  line-height: 1.2;
}/*# sourceMappingURL=notFound.css.map */