.add-modal {
  font-family: "PT Sans Caption", sans-serif;
  width: 100%;
}
.add-modal__stats {
  cursor: text;
  text-align: center;
  margin-bottom: 10px;
}
.add-modal__product-list {
  background-color: #FFFFFF;
  max-height: 300px;
  min-height: 300px;
  overflow-y: scroll;
  -ms-overflow-style: none;
  scrollbar-width: none;
  margin-bottom: 20px;
}
.add-modal__product-list::-webkit-scrollbar {
  width: 0;
}
.add-modal__product-list .cart-product:not(:last-child) {
  border-bottom: 1px solid #E4E4E4;
}
.add-modal__actions {
  display: flex;
  justify-content: center;
  gap: 20px;
}
.add-modal__product-count, .add-modal__total-price {
  font-weight: 700;
  text-wrap: nowrap;
}/*# sourceMappingURL=addProductModal.css.map */