.button {
  cursor: pointer;
  font-family: "PT Sans Caption", sans-serif;
  font-size: 16px;
  color: white;
  background-color: #037CD4;
  border-radius: 8px;
  padding: 7px 33px;
  border: none;
  outline: none;
  transition: all 0.4s ease;
  line-height: 1.2;
}
.button:hover {
  background-color: #036bb6;
}

.icon-button {
  position: relative;
}
.icon-button__icon {
  display: block;
  position: absolute;
  width: 16px;
  height: 16px;
  left: 11px;
  top: 8px;
}/*# sourceMappingURL=IconButton.css.map */