.cart {
    cursor: pointer;
    position: relative;
    font-family: "PT Sans Caption", sans-serif;
    width: 28px;
    height: 28px;

    &__img {
        width: 100%;
        display: block;
    }

    &__label {
        position: absolute;
        width: 24px;
        height: 24px;
        background-color: #037CD4;
        border-radius: 50%;

        top: -10px;
        right: -12px;
    }

    &__counter {
        position: absolute;
        display: block;
        font-size: 12px;
        font-weight: 700;
        color: #FFFFFF;

        top: 50%;
        right: 50%;
        transform: translateY(-50%) translateX(50%);
    }

    &__dropdown {
        cursor: default;
        visibility: hidden;
        opacity: 0;
        z-index: 50;
        position: absolute;
        background-color: #FFFFFF;
        border: 1px solid #E4E4E4;
        border-radius: 8px 8px 8px 8px;
        box-shadow: 1px 1px 6px rgba(0, 0, 0, 0.2);
        padding: 15px;

        top: 42px;
        right: -10px;

        transition: all .3s ease;

        &::before {
            content: "";
            position: absolute;
            z-index: 50;
            top: -8px;
            right: 15px;
            width: 16px;
            height: 16px;
            background-color: #FFFFFF;
            box-shadow: -2px -2px 3px -1px rgba(0, 0, 0, 0.2);
            transform: rotateZ(45deg);
        }
    }

    &__desc {
        font-size: 14px;
        line-height: 1.2;
        padding-bottom: 10px;
        border-bottom: 1px solid #E4E4E4;
    }

    &__product-count,
    &__total-price {
        font-weight: 700;
        text-wrap: nowrap;
    }

    &__stats {
        text-align: center;
        cursor: text;
    }

    &__empty {
        cursor: default;
        font-size: 16px;
    }

    &__empty-warning {
        text-wrap: nowrap;
        margin-bottom: 5px;
    }

    &__empty-img {
        width: 100px;
        margin: 0 auto;

        & img {
            display: block;
            width: 100%;
        }
    }

    &__text-link {
        text-wrap: nowrap;
        color: #037CD4;
    }

    &:hover {

        .cart__dropdown {
            visibility: visible;
            opacity: 1;
        }
    }
    

    &__product-list {
        background-color: #FFFFFF;
        max-height: 300px;
        overflow-y: scroll;
        -ms-overflow-style: none;
        scrollbar-width: none;
        

        &::-webkit-scrollbar {
            width: 0;
        }

        & .cart-product:not(:last-child) {
            border-bottom: 1px solid #E4E4E4;
        }

    }

    &__actions {
        display: flex;
        justify-content: center;
        padding-top: 20px;
        border-top: 1px solid #E4E4E4;
    }

    &__order-button {
        text-align: center;
    }
}