.not-found {
    text-align: center;
    font-family: "PT Sans Caption", sans-serif;
    
    &__image {
        display: inline-block;
        width: 30%;
        opacity: 0.2;
        margin-bottom: 30px;

        & img {
            display: block;
            width: 100%;
        }
    }

    &__title {
        color: #000000;
        font-size: 26px;
        font-weight: 700;
        margin-bottom: 5px;
    }

    &__description {
        color: #A7A7A7;
        font-size: 16px;
        line-height: 1.2;
    }
    
}